@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:100,300,400,500,700,900|Sen:400,700&display=swap&subset=korean,latin-ext);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@charset "utf-8";

html, body { margin: 0; width: 100%; height: 100%; position:relative; padding: 0; }
* {margin:0; padding:0; list-style:none; text-decoration:none; font-family: 'Noto Sans KR', sans-serif; font-size: 16px; outline: none; -webkit-text-size-adjust: none; box-sizing: border-box;}
ul>li>a { text-decoration:none; }
a {color: #333; text-decoration: none; outline: none}
table { border-collapse: collapse; }
textarea {box-sizing: border-box; resize: none;}
select {-webkit-border-radius: 0; -webkit-appearance: none; -moz-appearance: none; -webkit-appearance: none; appearance: none; box-sizing: border-box; cursor: pointer;}
strong,em,p,h1,h2,h3,h4,span,td,button{font-style:normal; color:#333; margin: 0; letter-spacing: -0.5px;}
ul { margin: 0;padding: 0;list-style: none;}
img {max-width:100%;}

.clear::after {
    content:""; 
    display:block; 
    clear:both;
}
header {
    background: #039DAE;
    position: fixed;
    width: 100%;
    height: 44px;
    padding: 14px 0 0 0;
    z-index: 100;
}
.header_activeHamburger__gUSV3,
.header_hamburger__3KC9a {
    position: absolute;
    top: 15px;
    left: 20px;
    width: 50px;
    height: 44px;
    transform:  scale(.3);
    transform-origin: top left;
    z-index: 2;
}

header > div > span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 4px;
    background-color: #fff;
    border-radius: 4px;
    transition: .4s ease;
}
header > div > span:nth-of-type(2) {
    top: 20px;
}
header > div > span:nth-of-type(3) {
    bottom: 0;
}
.header_activeHamburger__gUSV3 span:nth-of-type(1) {
    transform : translateY(20px) rotate(-315deg);
}
.header_activeHamburger__gUSV3 span:nth-of-type(2) {
    opacity : 0;
}
.header_activeHamburger__gUSV3 span:nth-of-type(3) {
    transform : translateY(-20px) rotate(315deg);
}
