header {
    background: #039DAE;
    position: fixed;
    width: 100%;
    height: 44px;
    padding: 14px 0 0 0;
    z-index: 100;
}
.activeHamburger,
.hamburger {
    position: absolute;
    top: 15px;
    left: 20px;
    width: 50px;
    height: 44px;
    transform:  scale(.3);
    transform-origin: top left;
    z-index: 2;
}

header > div > span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 4px;
    background-color: #fff;
    border-radius: 4px;
    transition: .4s ease;
}
header > div > span:nth-of-type(2) {
    top: 20px;
}
header > div > span:nth-of-type(3) {
    bottom: 0;
}
.activeHamburger span:nth-of-type(1) {
    transform : translateY(20px) rotate(-315deg);
}
.activeHamburger span:nth-of-type(2) {
    opacity : 0;
}
.activeHamburger span:nth-of-type(3) {
    transform : translateY(-20px) rotate(315deg);
}
.logo {
    display: block;
    width: 141px;
    height: 22px;
    margin: 0 auto;
    text-indent: -9999px;
    background: url('./logo.png')no-repeat center;
    background-size: cover;
}
.on_nav,
.nav {
    position: fixed;
    width: 232px;
    height: 100vh;
    padding: 0 0 0 20px;
    background: #fff;
    top: 0;
    left: -232px;
    overflow: auto;
    transition: .5s ease;
    z-index: 10;
}
.on_nav {
    left: 0;
}
.on_nav > div,
.nav > div {
    position: relative;
    padding: 20px 0 0 20px;

}
.menu_logo {
    display: inline-block;
    width: 23px;
    height: 14px;
    text-indent: -9999px;
    background: url('./menu_logo.png')no-repeat center;
    background-size: cover;
}
.btn_close {
    position: absolute;
    width: 12px;
    top: 20px;
    right: 20px;
}
.btn_close img {
    float: right;
}